import { APPNAME_PAGE_PREFIX } from './../../../services/constants'; 
import { Component, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Session, NativeService, StorageService, Utility } from "../../../services/services";

/**
 * Generated class for the MycodePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-mycode',
  templateUrl: 'mycode.html',
})
export class MycodePage {

  currentUser: any;
  staffNumber: string;
  isLogin: Boolean = false;
  isEmployee: Boolean = false;

  constructor(public navCtrl: NavController,
    public session: Session,
     public navParams: NavParams,
     public cd: ChangeDetectorRef) {
    this.isEmployee = this.session.isEmployee;
    if (this.session.user != null) {
      this.currentUser = this.session.user;
      this.isLogin = true;
    }

    if (this.currentUser && this.currentUser.employeeInfo) {
      this.currentUser.pictureUrl = this.currentUser.pictureUrl || "assets/img/avatar-round.png";
      this.staffNumber = this.currentUser.employeeInfo.staffNumber;
      // this.currentUser.scanCode="assets/img/code.png";
    } 
  }

  
  ionViewDidLoad() {
    let str = `${APPNAME_PAGE_PREFIX.toLowerCase()}AppPageId=ContactDetailPage?friendId=${this.session.userId}`;
    Utility.generateQRCode(str).then(res=>{
      this.currentUser.scanCode = res;
      this.cd.detectChanges();
      // this.openPage(str);
    })
  }

  openPage(str){
    Utility.parsePageUrl(str);
  }
}



